import React, { Component } from 'react';
import Card from 'react-bootstrap/Card';
import card_bg from '../../../../assets/card_bg.png';
import card_bg_alt from '../../../../assets/card_bg_alt.png';
import card_bg_big from '../../../../assets/card_bg_big.png';
import './ServiceCard.css';

class ServiceCard extends Component {

  render() {
    const image = this.props.size === 'big' ? card_bg_big : this.props.alt ? card_bg_alt : card_bg;
    return (
      <Card className="bg-dark text-white">
        <Card.Img src={image} alt='Card image' />
        <Card.ImgOverlay>
          <Card.Title>{ this.props.title }</Card.Title>
          { this.props.text && <Card.Text>{ this.props.text }</Card.Text> }
        </Card.ImgOverlay>
      </Card>
    );
  }
}

export default ServiceCard;
