import React, { Component } from 'react';
import ServiceCard from './ServiceCard';
import ServiceForm from './ServiceForm';
import './Info.css';

class Info extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 0
    };
  }

  componentDidMount() {
    var tabs = document.querySelectorAll('.service-tab');
    tabs.forEach(function(tab, index) {
      tab.addEventListener('click', function() {
        tabs.forEach(function(tab) {
          tab.classList.remove('selected');
        });
        tab.classList.add('selected');
        this.setState({
          selectedTab: index
        });
      }.bind(this));
    }.bind(this));
  }

  createCard(card, index) {
    return <ServiceCard key={ 'card_' + index }
                        title={ card.title }
                        text={ card.text }
                        size={ card.size }
                        alt={card.alt} />;
  }

  createForm(form) {
    return <ServiceForm title={ form.title }/>;
  }

  getContent(selectedTab) {
    if (selectedTab === 0) {
      const cards = [
        {title: 'Educación nutricional'},
        {title: 'Sobrepeso y obesidad'},
        {title: 'Nutrición clínica para patologías', text: 'Especialidad en patología renal'},
        {title: 'Nutrición en etapas de la vida', text: 'Especialidad en tercera edad'},
        {title: 'Alimentación vegetariana/vegana'},
      ];
      return {
        cards: cards.map(this.createCard),
        form: this.createForm({title: 'Pide cita o más información:'})
      };
    }

    if (selectedTab === 1) {
      const cards = [
        {title: 'Formaciones'},
        {title: 'Charlas'},
        {title: 'Talleres'},
        {title: 'Conferencias'}
      ];
      return {
        cards: cards.map(this.createCard),
        form: this.createForm({title: 'Si te interesa o quieres saber más:'}),
        extraInfo: '*Temáticas, contenido, metodología y duración según objetivos y necesidades de la audiencia.'
      };
    }

    if (selectedTab === 2) {
      const cards = [
        {
          title: 'Valoración, supervisión y elaboración de menús', 
          text: 'Para centros, comedores, cafeterías…',
          size: 'big'
        },
        {title: 'Formaciones o sesiones periódicas', text: 'Para trabajadores y/o usuarios'},
        {title: 'Asesoramiento transversal', text: 'En programas de salud y mejora de hábitos'},
      ];
      return {
        cards: cards.map(this.createCard),
        form: this.createForm({title: 'Si te interesa o quieres saber más:'})
      };
    }
  }

  render() {
    const content = this.getContent(this.state.selectedTab);

    return (
      <div className='Info'>
        <div className='services-row'>
          <div id='personalized-consultation' className='service-tab selected'>
            <div className='service-image'></div>
            <div className='service-title'>
              Consulta personalizada, online y presencial
            </div>
          </div>
          <div id='workshops' className='service-tab'>
            <div className='service-image'></div>
            <div className='service-title'>
              Formaciones y talleres
            </div>
          </div>
          <div id='colaborations' className='service-tab'>
            <div className='service-image'></div>
            <div className='service-title'>
              Colaboración con asociaciones, centros y empresas
            </div>
          </div>
        </div>
        <div className='service-description'>
          <div className='card-col'>
            { content.cards }
            <div className='card-col-info'>{ content.extraInfo }</div>
          </div>
          <div className='form-col'>{ content.form }</div>
        </div>
      </div>
    );
  }
}

export default Info;
